import React from 'react';
import { Select, ThemingProps } from '@deity/falcon-ui';

export type SelectInputOption = {
  value: string | number;
  label: string;
};

const OptionList = React.memo<{ options: SelectInputOption[] }>(({ options }) => (
  <>
    {options.map(x => (
      <option key={`${x.value}-${x.label}`} value={x.value}>
        {x.label}
      </option>
    ))}
  </>
));

/**
 * @see https://stackoverflow.com/a/49020529/412319
 * @internal
 */
const Placeholder = ({ value }) => (
  <>
    <option key={`${value}-a`} hidden>
      {value}
    </option>
    <option key={`${value}-b`} disabled {...{ default: true }}>
      {value}
    </option>
  </>
);

export type SelectInputProps = ThemingProps &
  React.InputHTMLAttributes<HTMLSelectElement> & {
    options: SelectInputOption[];
  };
export const SelectInput: React.FC<SelectInputProps> = React.memo(({ options, placeholder, ...rest }) => {
  return (
    <Select {...rest}>
      {placeholder && <Placeholder value={placeholder} />}
      <OptionList options={options} />
    </Select>
  );
});
