import React, { useMemo } from 'react';
import { useI18n } from '@deity/falcon-i18n';
import { useFalconScope } from '@deity/falcon-front-kit';
import { Picker } from '../Picker';

export const FalconScopePicker = props => {
  const { t } = useI18n();
  const { available, current, set } = useFalconScope();

  const options = useMemo(() => available.map(x => ({ value: x, label: t(`scopes.${x.id}`) })), [available, t]);
  const selected = {
    value: current,
    label: t(`scopes.${current.id}`)
  };

  if (options.length > 1) {
    return <Picker options={options} selected={selected} onChange={x => set(x.id)} {...props} />;
  }

  return null;
};
