import React from 'react';
import { JsonLd } from './JsonLd';

function getWordCount(string) {
  return string.trim().split(/\s+/).length;
}

function addBackslash(slug) {
  return slug[0] === '/' ? slug : `/${slug}`;
}

export interface AuthorSchemaData {
  '@type': string;
  name: string;
}

export interface BlogSchemaData {
  '@context': string;
  '@type': string;
  headline: string;
  image?: string;
  url?: string;
  dateCreated?: string;
  dateModified?: string;
  description?: string;
  articleBody?: string;
  wordcount?: number;
  author?: AuthorSchemaData;
}

type BlogDataForSchema = {
  slug: string;
  title: string;
  date: string;
  modified: string;
  excerpt: string;
  image?: { url: string };
  content?: string;
  author?: { name: string };
};

/**
 * Get the schema data for a blog.
 * @param  blog
 */
const getBlogSchemaData = <B extends BlogDataForSchema>(blog: B) => {
  if (!blog) {
    return null;
  }
  const schema: BlogSchemaData = {
    '@context': 'https://schema.org',
    '@type': 'BlogPosting',
    headline: blog.title,
    dateCreated: blog.date,
    dateModified: blog.modified,
    description: blog.excerpt
  };

  if (blog?.content) {
    schema.articleBody = blog.content;
    schema.wordcount = getWordCount(blog.content);
  }

  if (blog?.image?.url) {
    schema.image = blog.image.url;
  }

  if (blog?.slug) {
    schema.url = addBackslash(blog.slug);
  }

  if (blog?.author?.name) {
    schema.author = {
      '@type': 'Person',
      name: blog.author.name
    };
  }

  return schema;
};

export const BlogPostSchema = ({ blog }) => {
  const schemaData = getBlogSchemaData(blog);
  return <JsonLd item={schemaData} />;
};
