import React from 'react';
import { Image as FalconImage, ImageProps as FalconImageProps } from '@deity/falcon-ui';

type ImageProps = {
  loading?: 'lazy' | 'eager' | 'auto';
} & FalconImageProps;
export const Image: React.FC<ImageProps> = props => {
  return <FalconImage {...props} />;
};
Image.defaultProps = {
  loading: 'lazy'
};
