import React from 'react';
import { useCurrency } from '@deity/falcon-front-kit';
import { JsonLd } from './JsonLd';

/**
 * Get the final price of a product
 * @param {*} price
 */
const getProductFinalPrice = price => {
  const { regular, special } = price;
  if (special > 0 && special < regular) {
    return special;
  }
  return regular;
};

/**
 * Get the image for a product
 * @param {*} product
 */
const getProductImage = product => {
  const { gallery = [], thumbnail = null } = product;
  if (gallery.length) {
    return gallery[0].thumbnail;
  }
  if (thumbnail) {
    return thumbnail;
  }
  return null;
};

export interface OffersSchemaData {
  '@type': string;
  priceCurrency: string;
  price: number;
  availability: string;
}

export interface ProductSchemaData {
  '@context': string;
  '@type': string;
  name: string;
  image: string;
  sku: string;
  description: string;
  offers: OffersSchemaData;
  url?: string;
}

type ProductDataForSchema = {
  name: string;
  sku: string;
  description: string;
  currency: string;
  price: number;
  gallery?: string[];
  thumbnail?: string;
  urlPath?: string;
};

/**
 * Get the schema data for a product.
 * @param product
 * @param fallbackCurrency currency to be used when product does not have one
 */
const getProductSchemaData = <P extends ProductDataForSchema>(product: P, fallbackCurrency) => {
  if (!product) {
    return null;
  }
  const schema: ProductSchemaData = {
    '@context': 'http://schema.org',
    '@type': 'Product',
    name: product.name,
    image: getProductImage(product),
    sku: product.sku,
    description: product.description,
    offers: {
      '@type': 'Offer',
      priceCurrency: product.currency ? product.currency : fallbackCurrency,
      price: getProductFinalPrice(product.price),
      availability: 'http://schema.org/InStock'
    }
  };

  if (product.urlPath) {
    schema.url = product.urlPath;
  }

  return schema;
};

export const ProductSchema = ({ product }) => {
  const { currency } = useCurrency();
  const schemaData = getProductSchemaData(product, currency);
  return <JsonLd item={schemaData} />;
};

export const ProductListSchema = ({ items }) => {
  const { currency } = useCurrency();
  if (!items.length) {
    return null;
  }
  const products = items.map(item => {
    return getProductSchemaData(item, currency);
  });

  const schemaData = {
    '@context': 'http://schema.org',
    '@graph': products
  };

  return <JsonLd item={schemaData} />;
};
