import React from 'react';
import { JsonLd } from './JsonLd';

export interface OrganizationSchemaData {
  logo: string;
  name: string;
  url: string;
  sameAs?: string[];
}

export const OrganizationSchema = <T extends OrganizationSchemaData>(props: T) => {
  const { logo, name, url, ...rest } = props;

  const schemaData = {
    '@context': 'https://schema.org',
    '@type': 'Organization',
    name,
    url,
    logo,
    ...rest
  };

  return <JsonLd item={schemaData} />;
};
