import React from 'react';
import { Navbar, NavbarItem, NavbarItemMenu, List, ListItem } from '@deity/falcon-ui';
import { RouterLink } from '../Router';

export type MenuNavbarItem = {
  name: string;
  urlPath: string;
  children?: MenuNavbarItem[];
};
export type MenuNavbarProps = {
  items: MenuNavbarItem[];
};

export const MenuNavbar: React.SFC<MenuNavbarProps> = ({ items }) => (
  <Navbar>
    {items.map(item => (
      <NavbarItem key={item.urlPath}>
        <RouterLink to={item.urlPath} p="sm">
          {item.name}
        </RouterLink>
        {item.children && item.children.length > 0 && (
          <NavbarItemMenu>
            <List
              width="full"
              display="grid"
              gridTemplateColumns={{
                xs: '1fr',
                sm: '1fr 1fr 1fr',
                md: '1fr 1fr 1fr 1fr',
                lg: '1fr 1fr 1fr 1fr 1fr'
              }}
            >
              {item.children.map(subItem => (
                <ListItem key={subItem.urlPath}>
                  <RouterLink to={subItem.urlPath} display="block" textDecoration="underline" py="xs">
                    {subItem.name}
                  </RouterLink>
                  {subItem.children && subItem.children.length > 0 && (
                    <List>
                      {subItem.children.map(nextSubItem => (
                        <ListItem key={nextSubItem.urlPath}>
                          <RouterLink to={nextSubItem.urlPath} display="block" py="xs">
                            {nextSubItem.name}
                          </RouterLink>
                        </ListItem>
                      ))}
                    </List>
                  )}
                </ListItem>
              ))}
            </List>
          </NavbarItemMenu>
        )}
      </NavbarItem>
    ))}
  </Navbar>
);
