import React, { useMemo } from 'react';
import { Region } from '@deity/falcon-shop-extension';
import { SelectInput, SelectInputProps } from '../Forms';

export type RegionPickerItem = Pick<Region, 'id' | 'code' | 'name'>;

export type RegionPickerProps = Omit<SelectInputProps, 'options' | 'onChange'> & {
  options: RegionPickerItem[];
  value: RegionPickerItem;
  onChange: (value: RegionPickerItem) => any;
};

export const RegionPicker: React.FC<RegionPickerProps> = ({ value, options, onChange, ...rest }) => {
  const selectOptions = useMemo(
    () =>
      options.map(({ id, code, name }) => ({
        value: id,
        label: name || code
      })),
    [options]
  );

  return (
    <SelectInput
      {...rest}
      value={value ? value.id : undefined}
      options={selectOptions}
      onChange={e => {
        return onChange(options.find(x => x.id === e.target.value));
      }}
    />
  );
};
