import React from 'react';
import PropTypes from 'prop-types';

// Json Linked Data - https://json-ld.org/
// Schema markup - https://schema.org/docs/gs.html
// Schema testing tool - https://search.google.com/structured-data/testing-tool/u/0/
export const JsonLd = ({ item }) => (
  <script type="application/ld+json" dangerouslySetInnerHTML={{ __html: JSON.stringify(item) }} />
);

JsonLd.propTypes = {
  item: PropTypes.object
};

JsonLd.defaultProps = {
  item: {}
};
